<template>
  <div class="batch-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button @click="handleAdd()" type="warning" size="small" icon="el-icon-plus">{{$t('common.add')}}</el-button>
    </div>
    <el-form :modal="envForm" label-position="right" size="small">
      <el-row :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="选择应用环境：" prop="name">
            <el-select v-model="envId" placeholder="请选择" @change="envChange">
              <el-option v-for="item in envOptions"
                :key="item.pkValue"
                :label="item.pkDesc"
                :value="item.pkValue">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-form :model="dateForm" ref="dateForm" label-width="140px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="LAST_RUN_DATE：">
            <el-input v-model.trim="dateForm.LAST_RUN_DATE" disabled></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="RUN_DATE：">
            <el-input v-model.trim="dateForm.RUN_DATE" disabled></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="NEXT_RUN_DATE：">
            <el-input v-model.trim="dateForm.NEXT_RUN_DATE" disabled></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" @click="updateDate" icon="el-icon-search" size="small">
            更新日期
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table :data="batchTableData[currentPage - 1]" stripe>
      <el-table-column align="center" prop="BATCH_CLASS" label="批处理">
      </el-table-column>
      <el-table-column align="center" prop="EOD_SOD" label="EOD/SOD">
      </el-table-column>
      <el-table-column align="center" prop="BATCH_DESC" label="批处理描述">
      </el-table-column>
      <el-table-column :label="$t('common.operation')" align="center" fixed="right" width="140">
        <template slot-scope="props">
          <el-button type="text" size="mini" icon="el-icon-caret-right" @click="runBatch(props.row)">运行
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="batchTotal">
    </el-pagination>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      envId: '',
      dateForm: {},
      infoRes: null,
      currentPage: 1,
      pageSize: 10
    }
  },
  computed: {
    ...mapState({
      envOptions: state => state.processing.envOptions,
      batchTableData: state => state.processing.batchTableData,
      batchTotal: state => state.processing.batchTotal
    })
  },
  methods: {
    ...mapActions('processing', ['getBatchOption', 'getBatchEvnInfo', 'getBatchTableData', 'getBatchDate', 'updateBatchDate']),
    async envChange(val) {
      const infoRes = await this.getBatchEvnInfo({ envId: val })
      this.infoRes = infoRes.data
      this.getBatchTableData({zk: this.infoRes.ZK, applicationGroup: this.infoRes.APP_GROUP})
      const dateRes = await this.getBatchDate({
        zk: this.infoRes.ZK,
        applicationGroup: this.infoRes.APP_GROUP
      })
      console.log(dateRes)
      this.dateForm = {
        RUN_DATE: dateRes.data.runDate,
        LAST_RUN_DATE: dateRes.data.lastRunDate,
        NEXT_RUN_DATE: dateRes.data.nextRunDate
      }
    },
    async updateDate() {
      if (!this.envId) {
        this.$message.warning('请选择应用环境')
        return
      }
      const params = Object.assign({
        zk: this.infoRes.ZK,
        applicationGroup: this.infoRes.APP_GROUP
      }, this.dateForm)
      await this.updateBatchDate(params)
      this.$message.success('更新日期成功')
    },
    // 批处理运行
    runBatch(props) {
      const query = {
        batchClass: props.BATCH_CLASS,
        zk: (this.infoRes && this.infoRes.ZK) || 'zookeeper://192.168.162.147:2191',
        applicationGroup: (this.infoRes && this.infoRes.APP_GROUP) || 'gaeaadmin'
      }
      this.$router.push({
        name: 'batchRun',
        params: {
          id: props.BATCH_CLASS
        },
        query
      })
    },
    handleCurrentChange(val) {
      this.currentPage = val
    }
  },
  created() {
    this.getBatchOption()
  }
}
</script>



